<template>
  <div class="container">
    <div :class="{'coupon-value-label':true,'home-style':forHomePage}" :style='{color:textColor}'>
      <span class="symbol" v-if="(couponType==='FIXED_CASH_COUPON' || couponType ==='REBATE_COUPON')">￥</span>
      <span class="integer" :style="{fontSize:fontSize}">{{couponDisplayValue | formatPriceInt}}</span>
      <span class="float" v-if="withFloat">{{couponDisplayValue | formatPriceFloat}}</span>
      <span class="sales" v-if="couponType==='DISCOUNT_COUPON'">折</span>
    </div>
  </div>
</template>
<script>
// 券类型
// FIXED_CASH_COUPON：定额满减券
// REBATE_COUPON：满减券
// DISCOUNT_COUPON：折扣券
// EQUITY_COUPON：权益券
// GIFT_COUPON：礼品券
export default {
  name: 'couponValueLabel',
  props: {
    couponValue: {
      type: String,
      default: '0'
    },
    couponType: {
      type: String,
      default: 'FIXED_CASH_COUPON'
    },
    forHomePage: {
      type: Boolean,
      default: false
    },
    maxFontSize: {
      type: String,
      default: '0.64'
    },
    textColor: {
      type: String,
      default: '#ED2856'
    }
  },
  computed: {
    fontSize() {
      if (this.forHomePage) {
        if (String(this.couponDisplayValue).length < 6) {
          return '0.40rem'
        } else if (String(this.couponDisplayValue).length === 6) {
          return '0.38rem'
        } else if (String(this.couponDisplayValue).length > 6) {
          return '0.32rem'
        }
        return this.maxFontSize + 'rem'
      }
      if (String(this.couponDisplayValue).length < 6) {
        return this.maxFontSize + 'rem'
      } else if (String(this.couponDisplayValue).length === 6) {
        return 0.875 * this.maxFontSize + 'rem'
      } else if (String(this.couponDisplayValue).length > 6) {
        return 0.68 * this.maxFontSize + 'rem'
      }
      return this.maxFontSize + 'rem';
    },
    withFloat() {
      const arr = this.couponDisplayValue.split('.')
      if (arr && arr.length > 1 && arr[1] !== '00' && arr[1] !== '0') {
        return true
      }
      return false
    },
    couponDisplayValue() {
      return this.couponType === 'DISCOUNT_COUPON' ? String(Number(this.couponValue) / 10) : this.couponValue
    }
  }
}
</script>

