          <span v-else>{{ item.couponValue | withFloat }}元优惠券</span>
            <div class="coupon-border-right">
              <svg t="1594361575575" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3209" ><path d="M415.623529 975.811765h60.235295c72.282353 0 132.517647-60.235294 132.517647-132.517647v-204.8h-6.02353c-78.305882 0-138.541176-60.235294-138.541176-132.517647s60.235294-132.517647 132.517647-132.517647h12.047059V180.705882c0-72.282353-60.235294-132.517647-132.517647-132.517647h-60.235295v24.094118h60.235295c60.235294 0 108.423529 48.188235 108.423529 108.423529v162.635294C499.952941 349.364706 439.717647 421.647059 439.717647 505.976471s60.235294 150.588235 144.564706 162.635294V843.294118c0 60.235294-48.188235 108.423529-108.423529 108.423529h-60.235295v24.094118z" fill="#2283E2" p-id="3210"></path></svg>
              </div>
            <div class="coupon-border-left"><svg t="1594263532124" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3312" ><path d="M608.376471 951.717647h-60.235295c-60.235294 0-108.423529-48.188235-108.423529-108.423529v-168.658824c84.329412-6.023529 150.588235-78.305882 150.588235-162.635294 0-84.329412-66.258824-156.611765-150.588235-162.635294V180.705882c0-60.235294 48.188235-108.423529 108.423529-108.423529h60.235295V48.188235h-60.235295C475.858824 48.188235 415.623529 108.423529 415.623529 180.705882v192.752942h12.047059c72.282353 0 132.517647 60.235294 132.517647 132.517647 0 72.282353-60.235294 132.517647-138.541176 132.517647H415.623529V843.294118c0 72.282353 60.235294 132.517647 132.517647 132.517647h60.235295v-24.094118z" fill="#2283E2" p-id="3313"></path></svg></div>
        </li>
      </ul>
    </div>
    <div class="right">
      <img class="more-img" :src="`${$cdnImageUrl}/cart/icon-more.png`" />
    </div>
    <van-popup
      v-if="showCoupon"
      v-model="showCoupon"
      position="bottom"
      :style="{height: '9.96rem', borderRadius: '.24rem .24rem 0px 0px',display: 'flex', flexDirection: 'column', alignItems: 'center'}">
      <div class="icon-close" @click.stop="tooglePopup(false)"/>
      <div class="popup-title-row">
        <img class="popup-img" :src="`${$cdnImageUrl}/itSolution/icon_solution_title.png`"/>
        <div class="popup-title">
          <strong style="font-weight: bold">活动介绍：</strong>{{couponDescription}}
        </div>
      </div>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text=""
        @load="onLoad"
        van-clearfix
      >
        <van-row>
          <van-col v-for="(item, index) in couponList" :key="index">
            <coupon-item :coupon-info="item" @updateCoupon="onLoad"/>
          </van-col>
        </van-row>
      </van-list>

    </van-popup>
  </div>
</template>
<script>
import couponItem from './coupon-item';
import personalApi from '@/http/service/index';
export default {
  name: 'coupon',
  props: {
    soluteId: Number,
    planCode: {
      type: String,
      default: '',
    },
    couponTitleList: {
      type: Array,
      default: () => [],
    },
  },
  components: {
    couponItem,
  },
  filters: {
    withFloat(value) {
      const v = String(value)
      const arr = v.split('.')
      if (arr && arr.length > 1 && arr[1] !== '00' && arr[1] !== '0') {
        return value
      }
      return arr[0]
    },
  },
  data() {
    return {
      couponList: [],
      showCoupon: false,
      loading: false,
      finished: false,
      couponDescription: '',
    };
  },
  mounted() {
  },
  methods: {
    tooglePopup(show) {
      console.log('popup-show:', show);
      this.showCoupon = show;
    },
    onLoad() {
      personalApi.getSolutionCoupons({ id: this.soluteId }).then(res => {
        if (res.success) {
          this.couponList = [...res.data];
          this.couponList.map(item => {
            item.startTime = item.startTime ? item.startTime.split(' ')[0] : '';
            item.endTime = item.endTime ? item.endTime.split(' ')[0] : '';
          });
          if (this.couponList.length) {
            this.couponDescription = this.couponList[0].activityDescription;
          }
          this.loading = false;
          this.finished = true;
        }
      });
    }
  },
}
</script>
