<template>
  <div class="coupon-item-container">
    <div class="left-row">
      <div class="card-tag"/>
      <!-- <div class="price-text">￥{{couponInfo.couponValue}}</div> -->
      <coupon-value-label :textColor='"#fff"' :maxFontSize='"0.38"' :couponValue='String(couponInfo.couponValue)' :couponType='String(couponInfo.couponType)'></coupon-value-label>
      <div class="price-des">{{couponInfo.description || ''}}</div>
    </div>
    <div class="right-row">
      <div class="right-title">
        {{couponInfo.couponTemplateName}}
      </div>
      <div class="right-second">
        {{`${couponInfo.scope === "PRODUCT" ? '单品  ' : ''}${couponInfo.description || ''}`}}
      </div>
      <div class="right-date">
        <template v-if="couponInfo.startTime">
          {{couponInfo.startTime | formatDateEndOfDay}}-{{couponInfo.endTime | formatDateEndOfDay}}
        </template>
        <template v-else>
          {{couponInfo.validityPeriod}}
        </template>
      </div>
      <div class="button-receive" v-if="couponInfo.displayType !== '2' && !isReceived" @click="receiveCoupon">
        点击领取
      </div>
      <div class="button-use" v-else @click="useCoupon">
        去使用
      </div>
      <div class="icon-received" v-if="couponInfo.displayType === '2' | isReceived"/>
    </div>
  </div>
</template>
<script>
import { receiveCoupon } from '@/http/service/shop'
import couponValueLabel from '@/components/couponValueLabel'

export default {
  name: 'coupon-item',
  components: { couponValueLabel },
  props: {
    couponInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      isReceived: false,

    };
  },
  methods: {
    receiveCoupon() {
      receiveCoupon({
        activityCode: this.couponInfo.activityCode,
        couponTemplateCode: this.couponInfo.couponTemplateCode,
        sourceChannel: this.couponInfo.sourceChannel,
        streetId: this.$store.getters['getlocationInfo'].townshipId
        // couponId: this.couponInfo.id,
        // couponFrom: 2,
      }).then(res => {
        if (res.success) {
          this.$emit('updateCoupon');
          this.$toast('领取成功');
          this.isReceived = true;
          // this.$toast(res.message);
          // 如果可以领取的优惠券数量为0，要手动将优惠券的的状态改成 去使用
          // let _hasNum = res.data.hasReceivePersonNum;
          // setTimeout(() => {
          //   this.getCouponsCenter()
          // }, 2000)
        } else {
          this.$toast(res.message)
        }
      })
    },
    useCoupon() {
      this.$router.push(`/usablecouponlist?activityCode=${this.couponInfo.activityCode}&couponTemplateCode=${this.couponInfo.couponTemplateCode}&couponType=${this.couponInfo.couponType}&sourceChannel=${this.couponInfo.sourceChannel}`);
    }
  },

}
</script>
