/** VR预览列表 */
export const getVRPreviews = param => get(URL.storeVRPreviewList, param);
/** 预售专区 */
export const getPreSales = (param, isPlatForm) => {
  if (isPlatForm) {
    return get(URL.platformPreSale, param);
  } else {
    return post(`${URL.preSaleList}?storeId=${param.storeId}`);
  }
}
